  margin: 20px; }

h2 {
  font-size: 3.5em;
  font-weight: 400; }

h3 {
  font-size: 2.5em;
  font-weight: 400; }

h4 {
  font-size: 1.5em;
  font-weight: 400; }

p {
  font-size: 1.2em;
